<template>
  <div class="pos">
   
    <el-row>
      <el-col :span='7' class='post-order' id='order-list' >
        <el-tabs>
          <el-tab-pane label="点餐" >
           <el-table border style="width:100%;" :data='tableData'>
             <el-table-column prop='goodsName' label='商品名称'></el-table-column>
             <el-table-column prop='count' label='量' width='60'></el-table-column>
             <el-table-column prop='price' label='金额' width='80'></el-table-column>
             <el-table-column  label='操作' width='120' fixed="right" >
              <template slot-scope='scope'>
               <el-button type='text' size='small' @click="addOrderList(scope.row)">增加</el-button>
               <el-button type='text' size='small' @click="delSingle(scope.row)">删除</el-button>
              </template>
             </el-table-column>                   
           </el-table>  
           <div class="totalDiv">
             <small>数量：</small>{{totalCount}}       <small>金额：</small>{{totalMoney}}元
           </div>
           <div class='div-btn'>
           
           <el-button type="danger" @click=" delAll()">删除</el-button>
           <el-button type="success" @click="checkOut()">结账</el-button>
           
           </div>        
          </el-tab-pane>
          <el-tab-pane label="挂单"></el-tab-pane>
          <el-tab-pane label="结账"></el-tab-pane>
        </el-tabs>
      </el-col>
     <el-col span='17'>
     <div class="food-area">商品区</div>
     <div v-for="goods in oftenGoods" class="oftenGoods"  @click="addOrderList(goods)">
      <ul>
       <li>
        <span>{{goods.goodsName}}</span>
        <span class="oftenGoods-price">￥{{goods.price}}元</span>
       </li>
      </ul>
     </div>
     <div class="goods-type">
       <el-tabs>
         <el-tab-pane label="汉堡">
          <div >
           <ul class="cookList">
            <li v-for="goods in type0Goods" @click="addOrderList(goods)" >
              <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
              <span class="foodName">{{goods.goodsName}}</span>
              <span class="foodPrice">￥{{goods.price}}元</span>
             </li>
            </ul>
           
          </div>
                          
         </el-tab-pane>
         <el-tab-pane label="小食">
             <div >
           <ul class="cookList">
            <li v-for="goods in type1Goods"  @click="addOrderList(goods)">
              <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
              <span class="foodName">{{goods.goodsName}}</span>
              <span class="foodPrice">￥{{goods.price}}元</span>
             </li>
            </ul>
           
          </div>
         </el-tab-pane>
         <el-tab-pane label="饮料">
             <div >
           <ul class="cookList">
            <li v-for="goods in type2Goods"  @click="addOrderList(goods)"> 
              <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
              <span class="foodName">{{goods.goodsName}}</span>
              <span class="foodPrice">￥{goods.price}}元</span>
             </li>
            </ul>
           
          </div>
         </el-tab-pane>
         <el-tab-pane label="套餐">
             <div >
           <ul class="cookList">
            <li v-for="goods in type3Goods"  @click="addOrderList(goods)">
              <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
              <span class="foodName">{{goods.goodsName}}</span>
              <span class="foodPrice">￥{{goods.price}}元</span>
             </li>
            </ul>
           
          </div>
         
         </el-tab-pane>
       
       </el-tabs>
     
     
     </div>
     
     
     </el-col>
    </el-row>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'pos', 
  data(){
     return {
      tableData:[   ],
      oftenGoods:[   ],
      type0Goods:[   ],
      type1Goods:[   ],
      type2Goods:[   ],
      type3Goods:[   ],
      totalCount:0,
      totalMoney:0
     }
  },
  created:function(){
     
     axios.get('http://jspang.com/DemoApi/oftenGoods.php')
     .then(response=>{
         this.oftenGoods=response.data
     })
     .catch(error=>{
         console.log('无法访问哟！')
     })
      axios.get('http://jspang.com/DemoApi/typeGoods.php')
     .then(response=>{
         this.type0Goods=response.data[0];
         this.type1Goods=response.data[1];
         this.type2Goods=response.data[2];
         this.type3Goods=response.data[3];
     })
     .catch(error=>{
         console.log('无法访问哟！')
     })


  },
 mounted:function(){
   var orderHeight=document.body.clientHeight;
 
   document.getElementById('order-list').style.height=orderHeight+'px';
 },
 methods:{
     addOrderList:function(goods){
         this.totalCount=0;
         this.totalMoney=0;
         let isHave=false;
         for(let i=0;i<this.tableData.length;i++){
             if(this.tableData[i].goodsId==goods.goodsId){
                 isHave=true
             }
         }
         if(isHave){
             let arr=this.tableData.filter(o=>o.goodsId==goods.goodsId);
             arr[0].count++;
            
         }else{
             let newGoods={goodsId:goods.goodsId,goodsName:goods.goodsName,price:goods.price,count:1 }
            this.tableData.push(newGoods);

         }
          this.getAllMoney();

     },
     checkOut:function(){
       if(this.totalCount!=0){
        this.totalCount=0;
         this.totalMoney=0;
         this.tableData=[];
         this.$message({
           message:'結賬成功',
           type:'success'
         })
       }else{
         this.$message.error('还未选择商品')
       }
     },
     delSingle:function(goods){
           this.tableData=this.tableData.filter(o=>o.goodsId!=goods.goodsId)
        this.getAllMoney();
     },
     delAll:function(){
        this.totalCount=0;
         this.totalMoney=0;
         this.tableData=[];
     },
     getAllMoney:function(){
        this.totalCount=0;
         this.totalMoney=0;
       if(this.tableData){
           this.tableData.forEach((element)=>{
            this.totalCount+=element.count;
            this.totalMoney=this.totalMoney+(element.price*element.count)
        })
       }
     }

 }
}
</script>


<style scoped>
.post-order{
  background-color:#fff;
  border-right:1px solid blue;
  height:100%;

}
.div-btn{
  margin-top:10px;
}
.food-area{
  height:30px;
  border-bottom:1px solid #666;
  background-color:#fff;
  padding:10px;
  text-align:left;

}

.oftenGoods ul li{
  float:left;
  height:20px;
  border:1px solid blue;
  list-style:none;
  padding:10px;
  margin:10px;
  background-color:#fff;
  cursor:pointer;
  
}
.oftenGoods ul li .oftenGoods-price{
  color:pink;
}
.goods-type{
  clear:both;
}
.cookList li{
       list-style: none;
       width:23%;
       border:1px solid #E5E9F2;
       height:auto;
      
       background-color:#fff;
       padding: 2px;
       float:left;
       margin: 2px;
       cursor:pointer;
 
   }
   .cookList li span{
       
        display: block;
        float:left;
   }
   .foodImg{
       width: 20%;
   }
   .foodName{
       font-size: 18px;
       padding-left: 10px;
       color:brown;
 
   }
   .foodPrice{
       font-size: 16px;
       padding-left: 10px;
       padding-top:10px;
   }
   .totalDiv{
       background-color:#fff;
       padding:10px;
       border-bottom:1px solid #aaa;
   }

</style>



